.ng2-c-list {
  background: #ffffff;
  list-style: none;
  position: relative; }

.ng2-c-list--sticky {
  overflow: hidden; }

.ng2-c-list__header {
  font-weight: 400;
  width: 100%; }

.ng2-c-list__header--sticky {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10; }

.ng2-c-list__header--primary {
  background: #f5f5f5;
  color: #28648a;
  font-size: 14px; }
  .ng2-c-list__header--primary .ng2-o-btn__content {
    font-size: 14px; }

.ng2-c-list__header--secondary {
  border-bottom: 1px solid #f5f5f5;
  box-sizing: border-box;
  color: #28648a;
  font-size: 22px;
  padding: 5px 0; }

.ng2-c-list__wrapper {
  background: #ffffff;
  box-sizing: border-box;
  max-height: 100%;
  -ms-overflow-style: none;
  overflow-y: scroll;
  position: relative; }

.ng2-c-list--without-group .ng2-c-list__wrapper {
  padding-top: 28px; }

.ng2-c-list__item {
  border-bottom: 1px solid #f5f5f5;
  display: block;
  font-size: 16px;
  line-height: 1.3;
  width: 100%; }
  .ng2-c-list__item:last-child {
    border-bottom: 0; }

.ng2-c-list__item--selected {
  background: rgba(245, 245, 245, 0.5); }

.ng2-c-list__group-header {
  background: rgba(204, 204, 204, 0.3);
  box-sizing: border-box;
  color: #28648a;
  display: block;
  font-size: 14px;
  padding: 10px;
  text-transform: uppercase;
  width: 100%; }

.ng2-o-box .ng2-c-list__item--selected {
  margin: auto -15px;
  padding-left: 15px;
  width: calc(100% + 15px); }

.ng2-o-box--padded .ng2-c-list__group {
  margin: auto -15px; }
  .ng2-o-box--padded .ng2-c-list__group .ng2-c-list {
    margin: auto 15px; }

.ng2-c-list__cell {
  box-sizing: border-box;
  padding: 15px 0; }
  .ng2-c-list__cell .ng2-o-calendar,
  .ng2-c-list__cell .ng2-o-attachment {
    margin-top: 5px; }
  .ng2-c-list__cell .ng2-o-attachment {
    display: inline-block; }

.ng2-c-list__cell--xs {
  padding: 5px; }

.ng2-c-list__cell--reset {
  padding: 0 !important; }

.ng2-c-list__cell--divider {
  border-left: 1px solid #f5f5f5; }

.ng2-c-list__cell--small {
  font-size: 14px;
  padding: 10px 0; }

.ng2-c-list--title {
  color: #28648a;
  display: block; }

.ng2-c-list--description {
  color: #28648a;
  display: block;
  font-size: 16px;
  word-break: break-all;
  word-break: break-word; }
  @media (min-width: 768px) {
    .ng2-c-list--description {
      font-size: 18px; } }

.ng2-c-list--share {
  color: #28648a;
  display: block;
  font-size: 18px;
  word-break: break-all;
  word-break: break-word; }

.ng2-c-list--account {
  color: #9b9b9b;
  display: block;
  font-size: 16px;
  word-break: break-all;
  word-break: break-word; }

.ng2-c-list--stock {
  color: #28648a;
  font-size: 22px; }

.ng2-c-list--amount {
  font-size: 18px; }

.ng2-c-list__cell--seperator {
  border-right: 1px solid #f5f5f5; }
